<template>
  <div class="login">
    <!--<div class="page"> -->
    <div class="onlylogin">
      <div class = "logo">
        <!-- <h1>{{ name }} </h1> -->
        <img src="../image/Logo_image.png" width="700" class="mainlogo1">
        <p>     , HisTime</p>
      </div>

      <form>
        <div class ="login_box">
          <button type="button" class="btn yellow" v-on:click="login()">LOGIN</button>
          <div class="text_box">
            <input type = "text" class="t1" placeholder="HISNET ID" v-model="input.id" v-on:keyup.enter="login()">
            <br />
            <input type = "password" class="t1" placeholder="HISNET PASSWORD" v-on:keyup.enter="login()" v-model="input.password">
          </div>
        
        </div>
        <br /><br /><br />
        <div class="check_box"><input type="checkbox" v-model="isSave" /> <p>  30 </p> </div>
      </form>
      
      <!--</div> -->
     </div>
      <div  v-show="isLoad" id="loading">
        <div class="container">
          <div class="item item-1"></div>
          <div class="item item-2"></div>
          <div class="item item-3"></div>
          <div class="item item-4"></div>
      </div>
      
      </div>

      
      <div class="help">
        <button id='btn' v-on:click="show" />
        


        <div v-show="showbox" id="helpMessage" >
            <center><p class="p1"><u> N O T I C E </u></p></center>
            <p class="p2">His Time<br> <br>   <br>.<br>
            HISNET  <br>, <br>    <br>
            <u>  <br></u> .<br>   
            <a href="/manual" target="_blank"></a> .
            </p>
        </div>
      </div>
      
  
  <div class = "footer">
  <center><Footer></Footer></center>
  </div>
  </div>
</template>


<script type="text/javascript">
import Footer from '../components/Footer.vue'


export default {
  name: 'Login', 
  components: {
    Footer
  },
  data() {
    return{
      name : 'HisTime',
      input :  {
          id : "",
          password : "",
      },
      isLoad: false,
      isSave: false,
      showbox:false,
      _response : {}
    }

  },
  methods: {
    login(){
      this.$cookies.config('30d')
      this.isLoad = true;
      this.$http.post('/api/login', {
        id : this.input.id,
        password : this.input.password
      }).then((response) => {
        if (response.status === 200 ) {
              this._response = response;
              if(response.data.student_id === 'nope'){//  
                alert("   .");
                this.input.id = '';
                this.input.password = '';
                this.isLoad = false;
                return;
              }
              else{//   
                this.setSession()//  
                  if(this.isSave){this.setCookies(response)}//30    =>  
                  else{this.$cookies.set('auth_save', false)}//   ->    
              }
              this.$router.replace({ name: "show"});
            }
          }, function (err) {//  
            alert(" . 21500582@handong.edu    :) ")
          });
          
    },
    show: function(){
        this.showbox=!this.showbox;

    },
    setCookies(){
      
      this.$cookies.set('auth_save', true)
      this.$cookies.set('name', this._response.data.name)
      this.$cookies.set('student_id', this._response.data.student_id)
      this.$cookies.set('auth', true)
    },
    setSession(){
      this.$session.start()
      this.$session.set('name', this._response.data.name)
      this.$session.set('student_id', this._response.data.student_id)
      console.log("login name : " + this._response.data.name)
      console.log("login studentid : " +this._response.data.student_id )
      this.$session.set('auth', true)
      console.log("set session")

    }

  },

}


    // login(){
    //   this.$http.get('/api/login').then((response) => {
    //     console.log(response.data);
    // })}
    

    // login() {
    //     if(this.input.username != "" && this.input.password != "") {//no space
    //         if(this.input.username == this.$parent.mockAccount.username && this.input.password == this.$parent.mockAccount.password) {//correct
    //             this.$emit("authenticated", true);
    //             this.$router.replace({ name: "show" });
    //         } else {
    //             console.log("The username and / or password is incorrect");//incorrect-error
    //         }
    //     } else {
    //         console.log("A username and password must be present");//space-error
            
    //     }
    // }
</script>

